* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: black;
  height: 100%;
  width: 100%;
  min-width: 100%;
  min-height: 100%;
  font-family: system-ui,-apple-system,segoe ui,Roboto,Helvetica,Arial,sans-serif,apple color emoji,segoe ui emoji;
}
a {
  text-decoration: none;
}
::-webkit-scrollbar {
  width: 0;
}


@media (min-width: 980px), (max-width: 980px){
  :root {
    --width-code: 100%;
    --margin-code: 40px 0 0 0;
  }
}
@media (min-width: 550px) {
  :root {
    --tw-display-pc: block;
    --display-navbar: -;
    --position-widht: fixed;
    --tw-display-mobail: none;
    --tw-display: ;
    --display-app: flex;
  }
}
@media (max-width: 550px) {
  :root {
    --display-app: -;
    --margin-code: 40px 0 0 0;
    --display-code: block;
    --width-code: 100%;
    --tw-display-pc: none;
    --display-navbar: none;
    --position-widht: none;
    --tw-display-mobail: block;
    --tw-display: none;
  }
}